import { createRouter, createWebHistory } from "vue-router";

import user from "@/router/user";
import community from "@/router/community";
import meetingRoom from "@/router/meetingRoom";
import reservation from "@/router/reservation";
import error from "@/router/error";
import admin from "@/router/admin";

const routes = [
  ...user,
  ...community,
  ...meetingRoom,
  ...reservation,
  ...error,
  ...admin
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

// # router  
const Swal = require("sweetalert2");

router.beforeEach((to, from, next) => {
  // console.log("#21# router   to: ", to);
  // console.log("#21# router   from: ", from);

  // ,   
  if (to.path == "/" || to.path == "/join" || to.path == "/notice" || to.path == "/open-consult") {
    next();
  }
  // -   ,  () 
  else if (localStorage.getItem("token") == null) {
    Swal.fire("SSEUB", ` .`, "error");
    next("/");
  }
  // -  ,   
  else {
    next();
  }
});

export default router;
