const api = apiInstance();
async function joinRoomSession(loginInfo, success, fail) {
    // . login   .
    console.log(loginInfo);
    await api
      .post(`/api/room/session`, {customSessionId: '403'}) 
      .then(success)
      .catch(fail);
  } 

  export {joinRoomSession}   