<template>
  <v-dialog v-model="reviewOpen" width="700">
    <template v-slot:activator="{ props }">
      <v-btn class="pr-5 pl-5" rounded="pill" v-bind="props"
        prepend-icon="mdi-pencil" color="primary" variant="tonal"
      ></v-btn>
    </template>
    <v-card>
      <v-toolbar
        class="pr-2 pl-2"
        color="primary"
        title=" "
      ></v-toolbar>
      <v-card-item>
        <v-sheet class="mt-2 mb-2 d-flex align-center">
          <v-card-title></v-card-title>
          <v-rating class="mr-2 ml-2"
            v-model="review.reviewGrade" color="yellow-darken-3"
            density="compact"
          ></v-rating>
          <span>{{ review.reviewGrade }}</span>
        </v-sheet>
      </v-card-item>
      <v-card-text>
        <v-textarea v-model="review.reviewComment" label="  ."
          variant="outlined" counter="400" maxlength="400" hide-details
        >
        </v-textarea>
      </v-card-text>
      <v-card-item class="pt-0">
        <v-checkbox v-model="review.reservationOpen" label="    ?" color="primary"
          density="compact" value=1 false-value=0
          messages="  ,  ,   .       ."
        ></v-checkbox>
      </v-card-item>
      <v-card-actions class="justify-end">
        <v-btn class="mb-3" variant="text" color="error" size="large" @click="reviewOpen = false"></v-btn>
        <v-btn class="mb-3" variant="text" color="primary" size="large" @click="modifyReview"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { registReview } from "@/api/meetingFinish.js";

export default {
  name: "FinishedReviewModify",
  data() {
    return {
      reviewOpen: false,
      review: {
        no: 0,
        reviewGrade: 0,
        reviewComment: null,
        reservationOpen: 0
      }
    }
  },
  props: {
    rno: Number,
    reviewGrade: Number,
    reviewComment: String,
    idx: Number
  },
  methods: {
    async modifyReview() {
      await registReview(this.review)
      .then((res) => {
        if (res) {
          this.reviewOpen = false;
          this.$swal.fire(
            '  ',
            ' .',
            'success'
          )
          const review = this.review;
          review["idx"] = this.idx;
          this.$emit("updateReview", review);
        } else {
          this.reviewOpen = false;
          this.$swal.fire(
            '  ',
            '  .    .',
            'error'
          );
          this.reviewOpen = true;
        }
      });
    }
  },
  created() {
    this.review.reviewGrade = this.reviewGrade;
    this.review.reviewComment = this.reviewComment;
    this.review.no = this.rno;
  }
}
</script>

