<template>
  <v-sheet class="mr-2" width="150">
    <v-select
      :items="['', '', '']"
      variant="outlined"
      density="compact"
      v-model="range"
      hide-details
    ></v-select>
  </v-sheet>
  <v-text-field
    class="mb-1"
    v-model="keyword"
    density="compact"
    variant="outlined"
    label=""
    append-inner-icon="mdi-magnify"
    single-line
    hide-details
  ></v-text-field>
  <v-table fixed-header hover>
    <thead>
      <tr>
        <th width="96"></th>
        <th width="320"></th>
        <th width="180"></th>
        <th width="180"></th>
        <th width="150"></th>
        <th width="90"></th>
      </tr>
    </thead>
    <tbody>
      <tr
        v-for="(user, index) in getSearchList"
        :key="index"
        style="cursor: pointer"
      >
        <td>{{ String(index).padStart(4, "0") }}</td>
        <td>{{ user.id }}</td>
        <td>{{ user.userName }}</td>
        <td>{{ user.userNickname }}</td>
        <td>{{ user.authorities[0].authorityName.substr(5, 10) }}</td>
        <td>
          <v-btn
            class="mr-3"
            variant="outlined"
            color="error"
            rounded="0"
            @click="withdrawal(user.id, index)"
          >
            
          </v-btn>
        </td>
      </tr>
    </tbody>
  </v-table>
</template>

<script>
// const userStore = "userStore";
const adminStore = "adminStore";
import { mapActions } from "vuex";

export default {
  name: "UserWithdrawalTable",
  props: {
    userList: Array,
  },
  data() {
    return {
      range: "",
      keyword: "",
    };
  },
  computed: {
    getSearchList() {
      if (this.range == "") {
        return this.userList.filter((v) => v.id.includes(this.keyword));
      } else if (this.range == "") {
        return this.userList.filter((v) => v.userName.includes(this.keyword));
      } else if (this.range == "") {
        return this.userList.filter(
          (v) => v.userNickname != null && v.userNickname.includes(this.keyword)
        );
      } else {
        return this.userList;
      }
    },
  },
  methods: {
    ...mapActions(adminStore, ["excuteAdminWithdrawalUser"]),
    // [@Method]  - 
    async withdrawal(id, index) {
      this.$swal
        .fire({
          title: "?",
          text: `  ${id}    .`,
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Yes",
        })
        .then(async (result) => {
          if (result.isConfirmed) {
            await this.excuteAdminWithdrawalUser(id);
            this.$emit("deleteRemoveUser", index);
            // location.reload();
          }
        });
    },
  },
};
</script>

<style></style>
