import {
  login,
  anyPermit,
  partPermit,
  withdrawal,
  getRecentlyReservation,
} from "@/api/user";
import VueJwtDecode from "vue-jwt-decode"; // ! JWT   : npm i vue-jwt-decode
import store from "@/store/index.js";
// import router from "@/router/index.js";

// sweetalert2 
const Swal = require("sweetalert2");

const userStore = {
  namespaced: true,
  state: {
    isLogin: false,
    isValidToken: false,
    userId: null,
    userInfo: null, //     user    state
    userAuth: null, //    
  },
  getters: {
    getIsLogin: (state) => {
      return state.isLogin;
    },
    getUserId: (state) => {
      return state.userId;
    },
  },
  mutations: {
    SET_IS_LOGIN: (state, isLogin) => {
      state.isLogin = isLogin;
      console.log("#SET_IS_LOGIN# isLogin : ", state.isLogin);
    },
    SET_IS_VALID_TOKEN: (state, isValidToken) => {
      state.isValidToken = isValidToken;
    },
    SET_USER_ID: (state, userId) => {
      //state.isLogin = true;
      state.userId = userId;
      console.log("#SET_USER_ID# userId : ", state.userId);
    },
    SET_USER_INFO: (state, userInfo) => {
      state.userInfo = userInfo;
      console.log("#SET_USER_INFO# userInfo : ", state.userInfo);
    },
    SET_USER_AUTH: (state, userAuth) => {
      state.userAuth = userAuth;
      console.log(
        "#SET_USER_AUTH#     : ",
        state.userAuth
      );
    },
  },
  actions: {
    // [@Method] 
    async excuteLogin({ commit }, loginInfo) {
      await login(
        loginInfo,
        ({ data }) => {
          // if)  
          if (data.response == "success") {
            //    ( ,  , ) 
            let token = data["token"];
            // console.log(
            //   "#userStore - excuteLogin#   - token: ",
            //   token
            // );
            // token  > id(email),  
            let email = VueJwtDecode.decode(token).sub;
            commit("SET_USER_ID", email);
            commit("SET_USER_AUTH", VueJwtDecode.decode(token).auth);
            //sessionStorage.setItem("token", token);
            localStorage.setItem("token", token);
            commit("SET_IS_VALID_TOKEN", true);
            commit("SET_IS_LOGIN", true);

            //      
            // [@Method]      
            // return  = userNickname
            var userName = "";
            store
              .dispatch("userStore/checkAnyPermit", null, { root: true })
              .then((res) => {
                userName = res;

                // [@Method]     
                store
                  .dispatch("mainPageStore/excuteGetReservationCount", null, {
                    root: true,
                  })
                  .then(() => {
                    //   alert 
                    // const id = email.split("@");
                    Swal.fire(
                      "SSEUB",
                      `${userName}  !`,
                      "success"
                    );
                  });
              });

            // else)  
          } else {
            // console.log("#userStore - excuteLogin#  ");
            commit("SET_IS_LOGIN", false);
            commit("SET_IS_VALID_TOKEN", false);
            commit("SET_USER_ID", null);
            commit("SET_USER_INFO", null);
            commit("SET_USER_AUTH", null);

            //      alert 
            Swal.fire(" ", `${data.message}`, "error");
          }
        },
        (error) => {
          console.log(error);
        }
      );

      return await Promise.resolve();
    },
    // [@Method] Kakao, Naver     
    excuteSocialLogin({ commit }) {
      commit("SET_IS_LOGIN", true);
      commit("SET_IS_VALID_TOKEN", true);
    },
    // [@Method]   
    // return userNickname (  null)
    async checkAnyPermit({ commit }) {
      console.log("#userStore - checkAnyPermit#    ");
      const token = localStorage.getItem("token");
      var userName = null;
      await anyPermit(
        token,
        ({ data }) => {
          console.log("#userStore - checkAnyPermit# ");
          commit("SET_USER_INFO", data);
          userName = data.userName;
        },
        (error) => {
          console.log("#userStore - checkAnyPermit# ");
          console.log(error);
        }
      );
      return await Promise.resolve(userName);
    },
    // [@Method] ,   
    async checkPartPermit({ commit }, userId) {
      console.log(
        "#userStore - checkPartPermit# ,    "
      );
      const token = localStorage.getItem("token");

      await partPermit(
        userId,
        token,
        ({ data }) => {
          console.log("#userStore - checkPartPermit# ");
          commit("SET_USER_INFO", data);
        },
        (error) => {
          console.log("#userStore - checkPartPermit# ");
          console.log(error);
        }
      );
    },
    // [@Method] 
    excuteLogout({ commit }) {
      console.log("#userStore - excuteLogout#  ");
      commit;

      // !      alert
      store.dispatch("userStore/excuteRecentlyReservationInfo", null, {
        root: true,
      });

      //   clear
      // store.dispatch("userStore/clearUserStoreInfo", null, {
      //   root: true,
      // });
    },
    // [@Method]  
    async excuteWithdrawal(context) {
      // #  (Kakao, Google)  
      store.dispatch("userOAuthStore/excuteWithdrawalKakao");
      store.dispatch("userOAuthStore/excuteWithdrawalGoogle");

      const info = {
        id: context.state.userId,
      };
      await withdrawal(
        info,
        ({ data }) => {
          // i)  
          if (data.response == "success") {
            console.log("#userStore#  : ", data);
            Swal.fire("SSEUB", `${data.message}`, "success");

            //  
            store.dispatch("userStore/excuteLogout", null, { root: true });
          }
          // ii)  
          else {
            console.log("#userStore#  : ", data);
            Swal.fire("SSEUB", `${data.message}`, "error");
          }
        },
        (error) => {
          console.log(error);
        }
      );
    },
    // [@Method]     SET
    setAutoLogin({ commit }, res) {
      // console.log("#userStore#     response: ", res);

      //    ( ,  , ) 
      let token = res.data.token;
      // token  > id(email),  
      let email = VueJwtDecode.decode(token).sub;
      commit("SET_USER_ID", email);
      commit("SET_USER_AUTH", VueJwtDecode.decode(token).auth);
      // sessionStorage.setItem("token", token);
      localStorage.setItem("token", token);
      commit("SET_IS_VALID_TOKEN", true);
      commit("SET_IS_LOGIN", true);

      //      
      // [@Method]      
      // return  = userNickname
      var userNickname = "";
      store
        .dispatch("userStore/checkAnyPermit", null, { root: true })
        .then((res) => {
          userNickname = res;

          // [@Method]     
          store
            .dispatch("mainPageStore/excuteGetReservationCount", null, {
              root: true,
            })
            .then(() => {
              //   alert 
              // const id = email.split("@");
              Swal.fire("SSEUB", `${userNickname}  !`, "success");
            });
        });
      //  
      // location.href = process.env.VUE_APP_BASE_URL;
    },
    // [@Method]   
    moveMainPage() {
      location.href = process.env.VUE_APP_BASE_URL;
    },
    // [@Method]      
    async excuteRecentlyReservationInfo({ commit }) {
      commit;
      // console.log(
      //   "#21#        - id: ",
      //   this.state.userStore.userId
      // );

      await getRecentlyReservation(
        this.state.userStore.userId,
        ({ data }) => {
          //     
          if (data.response == "success" && data.reservationPetName != null) {
            Swal.fire({
              title: "<strong>   .</strong>",
              icon: "info",
              html:
                "<b></b>: " +
                "[" +
                data.reservationPetType +
                "] " +
                data.reservationPetName +
                "<br>" +
                "<b> </b>: " +
                data.reservationDate.substring(0, 16) +
                "<br>" +
                "<b></b>: " +
                data.reservationConsultName +
                "",
              showDenyButton: true,
              confirmButtonText: "",
              denyButtonText: ``,
            }).then((result) => {
              if (result.isConfirmed) {
                store.dispatch("userStore/clearUserStoreInfo", null, {
                  root: true,
                });
              }
            });
          } else {
            store.dispatch("userStore/clearUserStoreInfo", null, {
              root: true,
            });
          }
        },
        (error) => {
          console.log(error);
        }
      );
    },
    // [@Method]      +  
    clearUserStoreInfo({ commit }) {
      commit("SET_IS_LOGIN", false);
      commit("SET_IS_VALID_TOKEN", false);
      commit("SET_USER_AUTH", null);
      commit("SET_USER_ID", null);
      commit("SET_USER_INFO", "");
      window.localStorage.clear();

      // userSocialStore    (email, nickname) 
      store.dispatch("initSocialUserInfo");

      // mainPageStore   
      store.dispatch("mainPageStore/initMainPageStore", null, { root: true });

      // router.push("/");
      location.href = `${process.env.VUE_APP_BASE_URL}`;
    },
  },
};

export default userStore;
