import { apiInstance } from "./index.js";
import store from "@/store/index.js";

// api instance 
const api = apiInstance();

// sweetalert2 
const Swal = require("sweetalert2");

// [GET]   
async function sendPhoneAuth(phoneNumber) {
  // await api.get(`/user/phone/auth`, { params: phoneNumber }).then((res) => {
  await api
    .get(`${process.env.VUE_APP_API_BASE_URL}/user/phone/auth`, {
      params: phoneNumber,
    })
    .then((res) => {
      if (res.data.response == "success") {
        let timerInterval;

        Swal.fire({
          // 
          title: "  ",
          // 
          html: '<input id="authNumber" class="swal2-input"><br>3    .<br>  <b></b>',
          //    3 = 18000ms
          timer: 180000,
          //    
          timerProgressBar: true,
          //    
          didOpen: () => {
            const b = Swal.getHtmlContainer().querySelector("b");
            timerInterval = setInterval(() => {
              b.textContent =
                String(
                  parseInt(Swal.getTimerLeft() / (1000 * 60)) % 60
                ).padStart(2, "0") +
                ":" +
                String(parseInt(Swal.getTimerLeft() / 1000) % 60).padStart(
                  2,
                  "0"
                );
            }, 100);
          },
          //    
          willClose: () => {
            clearInterval(timerInterval);
          },
          //     
          confirmButtonText: "",
          cancelButtonText: "",
          showCancelButton: true,
          showCloseButton: true,
          focusConfirm: false,
          //    
          preConfirm: async () => {
            confirmPhoneAuth(
              document.getElementById("authNumber").value,
              phoneNumber.userPhone
            );
          },
        }).then((result) => {
          /* Read more about handling dismissals below */
          if (result.dismiss === Swal.DismissReason.timer) {
            console.log("I was closed by the timer");
          }
        });
      }
    });
}

