  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  padding: 15px;
  width: 300px;
  height: 500px;
}
.login .social-login {
  width: 250px;
  height: 160px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.login .social-login .icon {
  width: 110px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.login .social-login .mini-title {
  align-self: flex-start;
}
</style>
