<template>
  <NowLoading v-if="!loaded"></NowLoading>
  <div v-else class="page max-page border-sheet-four">
    <div class="page-inner">
      <div class="page-inner-title max-page border-sheet-four">
        <v-icon class="mr-2" size="x-large">mdi-text-box-plus</v-icon>
        <h2>  </h2>
      </div>
      <div class="page-inner-items max-page border-sheet-four pa-5">
        <v-form class="max-page" ref="reservationForm">
          <v-row class="d-flex flex-row justify-space-around">
            <v-hover v-slot="{ isHovering, props }">
              <v-card class="ma-3 mb-2 mr-1 pa-2 d-flex justify-space-between flex-column"
                width="690" height="430" variant="outlined"
                :elevation="isHovering ? 8 : 0" :class="{ 'on-hover': isHovering }"
                rounded="0" v-bind="props"
              >
                <v-card-title class="d-flex flex-row align-center">
                  <h4 class="mr-2">  </h4>
                  <RegistPet @addPetList="addPetList"></RegistPet>
                </v-card-title>
                <!-- {{ selectedPet }}    -->
                <v-item-group
                  selected-class="bg-primary"
                  v-model="selectedPet"
                  mandatory
                >
                  <v-container>
                    <v-sheet class="pl-4" height="280">
                      <v-row class="d-flex flex-row justify-start align-start">
                        <RegisterPetBig v-if="petList.length < 4" @addPetList="addPetList"></RegisterPetBig>
                        <v-sheet class="mt-2 mb-4 mr-5" v-for="pet in historyList" :key="pet.no" height="120" width="300">
                          <v-item v-slot="{ selectedClass, toggle }" :value="pet">
                            <v-card :class="[selectedClass]"
                              dark height="120" width="300" @click="toggle" variant="outlined" rounded="0"
                            >
                              <template v-slot:title>
                                {{ pet.petName }}
                              </template>
                              <template v-slot:prepend>
                                <v-avatar color="#06BEE1" size="90">
                                  <span v-if="pet.petImage == null">{{ pet.petName }}</span>
                                  <img v-else :src="getImageUrl(pet.petImage)" height="90" width="90" />
                                </v-avatar>
                              </template>
                              <template v-slot:subtitle>
                                {{ pet.petType }}<br />
                                {{ pet.petVariety }}<br />
                                {{ pet.petBirth }}
                              </template>
                            </v-card>
                          </v-item>
                        </v-sheet>
                      </v-row>
                    </v-sheet>
                    <v-pagination class="pagination mt-3"
                      v-model="page" :length="pages" @update:modelValue="updatePage"
                      rounded="0" size="35"
                    >
                    </v-pagination>
                  </v-container>
                </v-item-group>
              </v-card>
            </v-hover>
            <v-hover v-slot="{ isHovering, props }">
              <v-card
                class="ma-3 mb-2 pa-2 d-flex justify-space-between flex-column"
                width="305"
                height="430"
                variant="outlined"
                :elevation="isHovering ? 8 : 0"
                :class="{ 'on-hover': isHovering }"
                rounded="0"
                v-bind="props"
              >
                <v-card-title><h4>  </h4></v-card-title>
                <v-card-item>
                  <!-- {{ date.toISOString() }}     -->
                  <!--        -->
                  <DatePicker v-model="date" mode="dateTime"
                    timezone="Asia/Pyongyang" color="indigo" :minute-increment="10"
                    :available-dates='{ start: new Date(new Date().setDate(new Date().getDate() + 1)), end: new Date(new Date().setMonth(new Date().getMonth() + 2)) }'
                  ></DatePicker>
                </v-card-item>
              </v-card>
            </v-hover>
          </v-row>
          <v-row class="d-flex flex-row justify-space-around">
            <v-hover v-slot="{ isHovering, props }">
              <v-card
                class="ma-3 mr-1 pa-2 d-flex justify-flex-start flex-column"
                width="585"
                height="360"
                variant="outlined"
                :elevation="isHovering ? 8 : 0"
                :class="{ 'on-hover': isHovering }"
                rounded="0"
                v-bind="props"
              >
                <v-card-title class="pa-0 ml-4 mr-4 mt-2 mb-2"
                  ><h4>  </h4></v-card-title
                >
                <v-card-item class="pa-0 ml-4 mr-4 mb-2">
                  <v-textarea
                    class="mt-2 mb-1"
                    label="  ( 500)"
                    :rules="rules"
                    no-resize
                    clearable
                    clear-icon="mdi-close-circle"
                    counter
                    variant="outlined"
                    rows="5"
                    row-height="15"
                    maxlength="500"
                    oninput="javascript: if (this.value.length > this.maxLength) 
                      this.value = this.value.slice(0, this.maxLength);"
                    v-model="reservation.reservationConsultContent"
                  ></v-textarea>
                  <v-file-input
                    v-model="files"
                    counter
                    multiple
                    show-size
                    chips
                    accept="video/*, image/*"
                    variant="outlined"
                  ></v-file-input>
                </v-card-item>
              </v-card>
            </v-hover>
            <v-hover v-slot="{ isHovering, props }">
              <v-card
                class="ma-3 pa-2 d-flex justify-center flex-column"
                width="410"
                height="360"
                variant="outlined"
                :elevation="isHovering ? 8 : 0"
                :class="{ 'on-hover': isHovering }"
                rounded="0"
                v-bind="props"
              >
                <v-card-title></v-card-title>
                <v-card-item>
                  <p>
                      ,     
                        .
                  </p>
                  <v-radio-group
                    v-model="radio1"
                    :rules="[(v) => v == 'radio1-yes' || ' .']"
                    color="primary"
                    inline
                  >
                    <v-label color="black"
                      > ?<sup>*</sup></v-label
                    >
                    <v-radio label="" value="radio1-yes"></v-radio>
                    <v-radio label="" value="radio1-no"></v-radio>
                  </v-radio-group>
                </v-card-item>
                <v-card-item>
                  <p> ,    .</p>
                  <v-radio-group
                    v-model="radio2"
                    color="primary"
                    :rules="[(v) => v == 'radio2-yes' || ' .']"
                    inline
                  >
                    <v-label color="black"
                      > ?<sup>*</sup></v-label
                    >
                    <v-radio label="" value="radio2-yes"></v-radio>
                    <v-radio label="" value="radio2-no"></v-radio>
                  </v-radio-group>
                </v-card-item>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    size="x-large"
                    rounded="0"
                    color="primary"
                    @click="registed"
                    variant="tonal"
                    class="pr-10 pl-10"
                  >
                    
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-hover>
          </v-row>
        </v-form>
      </div>
    </div>
  </div>
</template>

<script>
import NowLoading from '@/views/NowLoading.vue';
import RegistPet from '@/components/CreateReservation/RegistPet.vue';
import RegisterPetBig from '@/components/CreateReservation/RegisterPetBig.vue'
import { mapState } from "vuex";
import { DatePicker } from 'v-calendar';
// import moment from 'moment';
import { apiInstance } from "@/api/index.js";
// const reservationStore = "reservationStore";
const userStore = "userStore";
import router from "@/router/index.js";

export default {
  name: "CreateReservation",
  computed: {
    ...mapState(userStore, ["userId"]),
    // ...mapState(reservationStore),
    pages() {
      if (this.pageSize == null || this.listCount == null) return 0;
      return Math.ceil(this.listCount / this.pageSize);
    },
  },
  components: {
    DatePicker,
    NowLoading,
    RegistPet,
    RegisterPetBig
  },
  data: () => ({
    loaded: false,
    registBtnActive: true,
    files:null,
    //    
    page: 1,
    pageSize: 4,
    petList: [],
    listCount: 0,
    historyList: [],
    rules: [
      (v) => v != null || "  .",
      (v) => v.length <= 500 || " 500",
    ],
    radio1: "radio1-no",
    radio2: "radio2-no",
    reservation: {
      userId: null,
      reservationPetNo: 0,
      reservationDate: null, //YYYY-MM-DD HH-mm-ss ex)2023-02-22 22:22:22
      reservationConsultContent: null,
    },
    selectedPet: null,
    date: new Date(new Date().setDate(new Date().getDate() + 1)),
    timeList: [],
  }),
  methods: {
    async addPetList(petInfo) {
      await this.petList.push(petInfo);
      await this.initPage();
      await this.updatePage(this.page);
    },
    async registed() {
      const { valid } = await this.$refs.reservationForm.validate();

      if (this.selectedPet == null) {
          this.$swal.fire(
            '  ',
            '   .   .',
            'error'
          )
      } 
      else if (valid && this.selectedPet != null) {
        this.createReservation();
      }
    },
    getImageUrl(img) {
      return `${process.env.VUE_APP_FILE_PATH_PET}${img}`;
    },
    async createReservation() {
      // process.env.VUE_APP_API_BASE_URL -> baseurl env 
      // timestamp
      this.reservation.reservationDate =
        this.date.getFullYear() +
        "-" +
        (this.date.getMonth() + 1) +
        "-" +
        this.date.getDate() +
        " " +
        this.date.getHours() +
        ":" +
        this.date.getMinutes() +
        ":" +
        this.date.getSeconds();
      this.reservation.reservationPetNo = this.selectedPet.no;
      this.reservation.userId = this.userId;

      // for (let i = 0; i < this.timeList.length; i++) {
      //   const diff = moment
      //     .duration(
      //       moment(
      //         this.reservation.reservationDate,
      //         "YYYY-MM-DD HH:mm:ss"
      //       ).diff(moment(this.timeList[i], "YYYY-MM-DD HH:mm:ss"))
      //     )
      //     .asMinutes();
      //   if (-30 < diff && diff < 30) {
      //     //console.log("  ");
      //     this.$swal.fire(
      //       "  ",
      //       "   . <br />   30   . <br />    .",
      //       "error"
      //     );
      //     return;
      //   }
      // }

      const frm = new FormData();
      frm.append("reservation",  new Blob([ JSON.stringify(this.reservation) ], {type : "application/json"}));
      if(this.files){
        console.log("")
        for(let i=0; i<this.files.length; i++){
          if(this.files[i].type.startsWith('image')){
            if(this.files[i].size <= 3*1024*1024){// 3
              frm.append("files", this.files[i]);
            } else {
              this.$swal.fire(
                "  ",
                "   . <br />    3MB.",
                "error"
              );
              return;
            }
          } else if (this.files[i].type.startsWith("video")) {
            if (this.files[i].size <= 50 * 1024 * 1024) {
              // 50
              frm.append("files", this.files[i]);
            } else {
              this.$swal.fire(
                "  ",
                "   . <br />    50MB.",
                "error"
              );
              return;
            }
          } else {
            console.log(" ");
          }
        }

        if (frm.getAll("files").length == 0) {
          alert("  .");
          return;
        }
      }
      console.log(frm);

      const api = apiInstance();
      await api.post(process.env.VUE_APP_API_BASE_URL+`/reservation`, frm, {
        headers: {'Content-Type': 'multipart/form-data'}
      }).then(() => {
        this.$swal.fire(
          '  ',
          '   . <br />                 .<br />       .',
          'success'
        )
        router.push("/receive-matching");
      }).catch(error => {
        console.log(error.message)
        this.$swal.fire(
          '  ',
          '   . <br />  ,     .',
          'error'
        )
        return;
      })
    },

    async petInfo(){
      var result = true;
      const api = apiInstance();
      await api
        .get(
          `${process.env.VUE_APP_API_BASE_URL}/reservation/pet-list/${this.userId}`
        )
        .then(({ data }) => {
          for (var i = 0; i < data.length; i++) {
            let petinfo = {};
            petinfo["no"] = data[i].no;
            petinfo["petName"] = data[i].petName;
            petinfo["petImage"] = data[i].petImage;
            petinfo["petType"] = data[i].petType;
            petinfo["petVariety"] = data[i].petVariety;
            if (data[i].petBirth != null) {
              petinfo["petBirth"] = data[i].petBirth.substr(0, 7);
            } else {
              petinfo["petBirth"] = " ";
            }
            this.petList.push(petinfo);
          }
          console.log(this.petList);
        })
        .catch((err) => {
          console.log(err);
        })
        return await Promise.resolve(result);
      },
    async getTimeList(){
      const api = apiInstance();
      await api.get(process.env.VUE_APP_API_BASE_URL+`/reservation/date-validation/${this.userId}`)
        .then(({ data }) => {
          for (var i = 0; i < data.length; i++) {
            this.timeList.push(data[i]);
          }
          //console.log(this.timeList);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    initPage: function () {
      this.listCount = this.petList.length;
      if (this.listCount < this.pageSize) {
        this.historyList = this.petList;
      } else {
        this.historyList = this.petList.slice(0, this.pageSize);
      }
    },
    updatePage: function (pageIndex) {
      let _start = (pageIndex - 1) * this.pageSize;
      let _end = pageIndex * this.pageSize;
      this.historyList = this.petList.slice(_start, _end);
      this.page = pageIndex;
    },
  },

  watch: {
    files() {
      //     
      if (this.files != null) {
        var maxLength = 30; //   default 30
        if (this.files.length > 4)
          maxLength = 6; //   4    6
        else if (this.files.length > 2) maxLength = 11; //   2    11

        //    
        for (var i = 0; i < this.files.length; i++) {
          //   
          var filename = this.files[i].name;

          // 
          var extension = filename
            .substring(filename.lastIndexOf("."), filename.length)
            .toLowerCase();

          //    
          filename = filename.substring(0, filename.lastIndexOf("."));

          //   
          Object.defineProperty(this.files[i], "name", {
            writable: true,
            value: filename.substr(0, maxLength) + String(i) + extension,
          });
        }
      }
    },
  },

  async created() {
    this.loaded = false;
    try {
      await this.petInfo().then((res) => {
        this.loaded = res;
      });
      this.getTimeList();
      this.initPage();
      this.updatePage(this.page);
    } catch (e) {
      console.error("#   ", e);
    }
  },
};
</script>

<style></style>
