import { apiInstance } from "./index.js";

const api = apiInstance();

// [GET]    
async function getOpenBoard() {
  var result = [];
  await api.get(`${process.env.VUE_APP_API_BASE_URL}/reservation/open`)
  .then((res) => {
    result = res.data;
  })
  .catch()
  return await Promise.resolve(result);
}

export { getOpenBoard };
