import { duplicateId } from "@/api/userJoin";
import {
  getKakaoToken,
  getKakaoUserInfo,
  withdrawalKakao,
  getGoogleInfo,
  withdrawalGoogle,
  sendKakaoMessage,
} from "@/api/userOAuth";
import store from "..";

const userOAuthStore = {
  namespaced: true,
  state: {
    kakaoToken: null,
    googleToken: null,
  },
  getters: {
    getKakaoToken: (state) => {
      return state.kakaoToken;
    },
    getGoogleToken: (state) => {
      return state.googleToken;
    },
  },
  mutations: {
    // #Kakao#  Kakao Token 
    SET_KAKAO_TOKEN: (state, kakaoToken) => {
      state.kakaoToken = kakaoToken;
      // #Kakao#   Kakao   
      getKakaoUserInfo(kakaoToken);
    },
    // #Google#  Google Token 
    SET_GOOGLE_TOKEN: (state, googleToken) => {
      state.googleToken = googleToken;
    },
  },
  actions: {
    // [@Method] #Kakao# Kakao Token  (Kakao   )
    async excuteKakaoToken({ commit }, code) {
      const kakaoInfo = {
        grant_type: "authorization_code",
        client_id: process.env.VUE_APP_OAUTH_KAKAO_CLIENT,
        redirect_uri: process.env.VUE_APP_OAUTH_REDIRECT_URI,
        code: code,
        client_secret: process.env.VUE_APP_OAUTH_KAKAO_CLIENT_SECRET,
      };

      await getKakaoToken(
        kakaoInfo,
        ({ data }) => {
          console.log(
            "#userOAuthStore - getKakaoToken# Kakao Token  : ",
            data
          );
          //  access-token      
          localStorage.setItem("kakaoToken", data.access_token);
          commit("SET_KAKAO_TOKEN", data.access_token);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    // [@Method] #Google# Google    >  OR 
    async excuteGoogleInfo({ commit }, token) {
      // Google Token 
      localStorage.setItem("googleToken", token);
      commit("SET_GOOGLE_TOKEN", token);

      await getGoogleInfo(
        token,
        async ({ data }) => {
          // console.log("#21# Google  : ", data);
          // console.log("#21# Google  email: ", data.email);
          const info = {
            id: data.email,
            nickname: "",
            provider: "GOOGLE",
          };

          var duplicateResult = false; //      (  )
          await duplicateId(info.id).then((res) => {
            duplicateResult = res;
          });
          // *        >   
          if (duplicateResult == true) {
            store.dispatch("setSocialUserInfo", info); // userSocialStore id(email) 
          }
          // *  > 
          else {
            //  JWT   > (userStore    )
            const loginInfo = {
              id: info.id,
              password: `${process.env.VUE_APP_OAUTH_GOOGLE}`,
              socialButton: 1,
            };
            store.dispatch("userStore/excuteLogin", loginInfo, { root: true });
          }
        },
        (error) => {
          console.log(error);
        }
      );
    },
    // [@Method] #Kakao# 
    async excuteWithdrawalKakao(context) {
      //if (context.state.kakaoToken == null) return;
      context;
      if (localStorage.getItem("kakaoToken") == null) return;
      console.log("#userOAuthStore# KAKAO  ");

      await withdrawalKakao(
        //context.state.kakaoToken,
        // localStorage.getItem("kakaoToken"),
        ({ data }) => {
          console.log(
            "#userOAuthStore - withdrawalKakao# Kakao  : ",
            data
          );
        },
        (error) => {
          console.log(
            "#userOAuthStore - withdrawalKakao# Kakao  : ",
            error
          );
        }
      );
    },
    // [@Method] #Google# 
    async excuteWithdrawalGoogle(context) {
      if (context.state.googleToken == null) return;
      console.log("#userOAuthStore# GOOGLE  ");

      await withdrawalGoogle(
        ({ data }) => {
          console.log(
            "#userOAuthStore - withdrawalGoogle# Google  : ",
            data
          );
        },
        (error) => {
          console.log(
            "#userOAuthStore - withdrawalGoogle# Google  : ",
            error
          );
        }
      );
    },
    // [@Method]    ()
    async excutedSendKakaoMessage() {
      console.log("#21#   ");
      await sendKakaoMessage(
        ({ data }) => {
          console.log("#21# Kakao   : ", data);
        },
        (error) => {
          console.log("#21# Kakao   : ", error);
        }
      );
    },
  },
};

export default userOAuthStore;
