<template>
  <v-dialog v-model="registBoardOpen" width="700">
    <template v-slot:activator="{ props }">
      <v-btn class="ma-2" rounded="pill" v-bind="props"
        prepend-icon="mdi-plus" color="primary" variant="tonal"
      >
         
      </v-btn>
    </template>
    <v-card class="pa-5">
      <v-card-title>
        <h3> </h3>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-form ref="forms">
            <v-text-field v-model="newBoard.boardTitle" label="" variant="underlined"
              maxlength="100" counter="100" :rules="boardRules.boardTitle"
            ></v-text-field>
            <v-textarea v-model="newBoard.boardContent" label=""
              variant="underlined" maxlength="500" counter="500"
              row-height="500" rows="8" auto-grow :rules="boardRules.boardContent"
            ></v-textarea>
            <v-file-input v-model="boardFile" label="" variant="underlined" :rules="boardRules.boardFile"></v-file-input>
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="error" variant="text" @click="registBoardOpen = false"></v-btn>
        <v-btn color="primary" variant="text" @click="validate()"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { apiInstance } from "@/api/index.js";
import { mapState } from "vuex";
const userStore = "userStore";
export default {
  name: "FreeBoardRegist",
  computed: {
    ...mapState(userStore, ["userId", "userInfo", "userAuth"]),
  },
  data() {
    return {
      registBoardOpen: false,
      boardFile: null,
      newBoard: {
        //   
        boardTitle: null,
        boardContent: null,
        //  
        userId: this.userId,
        userNickname: "",//this.userInfo.usreNickname,// ?
        boardFlag: this.userAuth=="ROLE_ADMIN"?0:this.userAuth=="ROLE_CONSULTANT"?1:2,
      },
      boardRules: {
        boardTitle: [
          (v) => (v != null && v.length > 0) || "  .",
          (v) => v.length <= 100 || "100   .",
        ],
        boardContent: [
          (v) => (v != null && v.length > 0) || "  .",
          (v) => v.length <= 500 || "500   .",
        ],
        boardFile: [
          (v) => v == null || v.length <= 1 || "  1  .",
          (v) => v == null || v[0].size <= 5000000 || "    5MB .",
        ]
      },
    }
  },
  methods: {
    async validate() {
      const { valid } = await this.$refs.forms.validate();
      if (valid) {
        const frm = new FormData();

        frm.append("boardContent", this.newBoard.boardContent);
        frm.append("boardFlag", this.newBoard.boardFlag);
        frm.append("boardTitle", this.newBoard.boardTitle);
        frm.append("userNickname", this.userInfo.userNickname);
        frm.append("userId", this.userId);
        if (this.boardFile != null || this.boardFile.length == 1) frm.append("file", this.boardFile[0]);

        const api = apiInstance();
        await api.post(process.env.VUE_APP_API_BASE_URL+`/board/community`, frm, {
          headers: {'Content-Type': 'multipart/form-data; charset=utf-8;'}
        }).then((data) => {
          const board={};
          board["no"] = data.data.no;
          board["userNickname"] = data.data.userNickname;
          board["boardTitle"] = data.data.boardTitle;

          this.$parent.pushList(board);
          this.$swal.fire(
            '  ',
            '   .',
            'success'
          )
          this.registBoardOpen = false;
        }).catch(error => {
          console.log(error.message)
          return;
        })
      }

    }
  }
}
</script>

